import React, { Fragment, useEffect } from 'react';
import "../Css/Swabhimaancard.css";
import card3 from "../Component/crm/assets/images/Swabhimaan Card/TwoCard.jpg"
import Cardhead from '../Component/crm/assets/images/Header/Card.jpg'
import Eligible from '../Component/crm/assets/images/Swabhimaan Card/Eligibility.jpg'
import SavingHub from '../Component/crm/assets/images/Swabhimaan Card/SavingHub.jpg'
import Payment from '../Component/crm/assets/images/Swabhimaan Card/Payment.jpg'
import Aos from 'aos';

function SwabhimaanCard() {
    useEffect(() => {
        Aos.init({
            duration: 2000
        })
    })
    return (
        <Fragment>
            <img src={Cardhead} alt="Card" className='w-100 ' data-aos='zoom-in' data-aos-duration="2200" data-aos-offset='300' />
            <div className="container">
                <div className="container" style={{ minWidth: "50vw", maxWidth: "50vh" }} data-aos='fade-up'>
                    <h2 className='fw-bold my-4 p-2 fs-1 text-center rounded p-2' style={{ color: "White", backgroundColor: "rgba(172,61,61)", fontFamily: "poppins", boxShadow: "2px 2px 0 0 black" }}>Key Feature</h2>
                </div>

                <SwabhimaanCard1></SwabhimaanCard1>
                <SwabhimaanCard2></SwabhimaanCard2>

                <SwabhimaanCard3></SwabhimaanCard3>

                <SwabhimaanCard4></SwabhimaanCard4>

            </div>

        </Fragment>
    )
}
export default SwabhimaanCard


export function SwabhimaanCard1() {
    return (
        <Fragment>
            <div className="container mb-5 d-xxl-flex d-xl-flex align-items-center" data-aos='fade-up' data-aos-duration="2200" data-aos-offset='300'>
                <div className="container">
                    <h3 className='fw-bold my-4 text-center rounded p-2' style={{ color: "White", fontFamily: "poppins", backgroundColor: "rgba(172,61,61)", boxShadow: "2px 2px 0 0 black" }}>Card Will Help People</h3>
                    <div className="card text-white bg-slate">
                        <div className="card-header"> <h1 className='h1 fs-2 fw-bold' style={{ color: 'orange', fontFamily: "sens serif" }}>Swabhimaan Card By FDA</h1></div>
                        <div className="card-body">
                            <p className='px-2 pt-2 fs-6' style={{ textAlign: "justify" }}>Swabhimaan Card by FDA  India is an innovative and revolutionary product in the market these days. It has been designed to provide financial assistance to the countryâ€™s people and agricultural businesses along with the every individual personal of country. This card will help people get access to the virtual money they need to purchase their needed supplies and investments for their farm ventures and intraday usable FMCG consumables needed for survivals in society. This card enables people to make payments of up to one lakh rupees without any hassles or lengthy paperwork. This card allows people to instantly pay for their supplies so that they can purchase them immediately and also save time and money. Additionally, this card provides people with access to competitive interest rates and insurance policies that are important for their farm investments.</p>
                        </div>
                    </div>
                </div>
                <div className="container text-center" data-aos='flip-left' data-aos-duration="2200" data-aos-offset='300'>
                    <img src={card3} className='w-75 sw-card1' alt="card4" />
                </div>
            </div>

        </Fragment>
    )
}

export function SwabhimaanCard2() {
    return (
        <Fragment>
            <div className="container d-xxl-flex d-xl-flex d-lg-flex align-items-center">
                <div className="container text-center" data-aos='flip-left' data-aos-duration="2200" data-aos-offset='300'>
                    <img src={Eligible} className='w-75 sw-card1' alt="card4" />
                </div>
                <div className="" data-aos='fade-up' data-aos-duration="2200" data-aos-offset='300' >
                    <div className="container my-3">
                        <h2 className='fw-bold text-center rounded p-2' style={{ color: "White", fontFamily: "poppins", backgroundColor: "rgba(172,61,61)", boxShadow: "2px 2px 0 0 black" }}>Eligibility Criteria</h2>
                    </div>
                    <div className="card text-white bg-slate">
                        <div className="card-header"><h1 className='h1 fs-2 fw-bold' style={{ color: 'orange', fontFamily: "sens serif" }}>Get Your Swabhimaan Card</h1>
                        </div>
                        <div className="card-body">
                            <ul>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Indian citizenship</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Above 20 yrs to 65 yrs (if borrowed after age window then it will automatically transferred to their son or daughter or wife)</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Valid kyc documents (Aadhar card, pan card, voter id card, school leaving certificate, PDS card per ration card or any doc issued by Govt. official organization).</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Income certificate.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Local address proof (Aadhar, voter id, gram panchayat certificate attested by head authority, domicile certificate, ration card , passport)</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Land possession certificate.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Cibil score. Existing loan borrower.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Live photo.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Applicant must have his own residence</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}




export function SwabhimaanCard3() {
    return (
        <Fragment>
            <div className="container d-xxl-flex d-xl-flex align-items-center my-4 ">
                <div className="" data-aos='fade-up'>
                    <div className="container my-3">
                        <h2 className='fw-bold text-center rounded p-2' style={{ color: "White", fontFamily: "poppins", backgroundColor: "rgba(172,61,61)", boxShadow: "2px 2px 0 0 black" }}>Payment Through Card</h2>
                    </div>
                    <div className="card text-white bg-slate mb-3" >
                        <div className="card-header"><h1 className='h1 fs-2 fw-bold text-center' style={{ color: 'Orange', fontFamily: "sens serif" }}>Swabhimaan Card</h1>
                        </div>
                        <div className="card-body">
                            <p className='text-start card-title' style={{ fontWeight: "500" }}>
                                Wallet balance of 1 lakh rupees starting first capping 10 thousand in 2nd cycle 25 thousand in 3rd cycle 37 thousand and in  4th cycle 76 thousand.
                            </p>
                            <ul className='ms-4'>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>First Emi will have 60 days looking period.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>70 days later first reminder should be sent to user.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>On 90th day they need to pay first Emi, And cycle continues.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Advance payment could also be done</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Minimum once in a financial year it should be used or maintenance charges will be impose as fine of 700 rupees per annum</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Rate of compound interest 2% per annum on failure of emi</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Balance can be added to the wallet via any payment gateway and banking wire system.</li>
                                <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>Direct recharge can be done by shop as well as from Swabhimaan Kendra.</li>
                                {/* <li className='ps-1 pt-2 fs-6' style={{ textAlign: "justify" }}>And put all required terms and conditions as per by laws of buy now pay later.</li> */}
                            </ul>
                            <p className='pt-2 fs-6 fst-italic fw-bold' style={{ color: "blue" }}>Note: <span className='text-left fs-6'>financial year refers as 1 April to next year 31st march of Christian calendar.</span></p>
                        </div>
                    </div>
                </div>
                <div className="container text-center" data-aos='flip-left' >
                    <img src={Payment} className='w-75 sw-card1' alt="Swabhimaan card" />
                </div>

            </div>
        </Fragment>
    )
}

export function SwabhimaanCard4() {
    return (
        <Fragment>
            <div className="container mt-5 p-4 d-xxl-flex d-xl-flex align-items-center" data-aos='fade-up' data-aos-duration="2200" data-aos-offset='300'>
                <div className="container text-center" data-aos='flip-left' >
                    <img src={SavingHub} className='w-75 sw-card1 rounded' alt="card2" />
                </div>
                <div className="" data-aos='fade-up' data-aos-duration="2200" data-aos-offset='300'>
                    <div className="container p-2">
                        <h2 className='fw-bold my-4 text-center rounded p-2 ' style={{ color: "White", backgroundColor: "rgba(172,61,61)", fontFamily: "poppins", boxShadow: "2px 2px 0 0 black" }}>Offers Through The Card</h2>
                    </div>
                    <div className="card text-white bg-slate mb-3">
                        <div className="card-header">
                            <h1 className='h1 fs-2 fw-bold' style={{ color: 'orange', fontFamily: "sens serif" }}>Swabhimaan Card Offers</h1>
                        </div>
                        <div className="card-body">
                            <ul>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>All available agricultural products (seeds, fertilizers organic and inorganic bio products, agricultural chemicals etc).</li>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>All FMCG products can be purchased available at store.</li>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>Buy now pay later option.</li>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>Daily consumable products can be purchased.</li>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>Emergency financial support system up to 1 lakh rupees cash disbursement terms and conditions applied ( if any sudden death happens to the card holders family or dependents for 1 year ).</li>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>Deposits system.</li>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>Social harmony support system (For the marriage of a child, up to 10 lakh can be availed for 5 years after using the card for 3 years without any EMI failure)</li>
                                <li className='ps-2 pt-2 fs-6' style={{ textAlign: "justify" }}>Purchasing and selling of crops.</li>
                            </ul>
                        </div>
                    </div>


                </div>
            </div>
        </Fragment>
    )
}
